<script>
import axios from 'axios';
import PaginationTableVue from './PaginationTable.vue';
export default {
    name: "TableComponent",
    components: {
        PaginationTableVue
    },
    props : {
        tableData : {
            type : Object,
            required : true
        },
        name : {
