export  function getData(api,tableName){
    //table[tableName].datas = [] ;
        var getIndex = function (name) {
            for(var i in table){
                if(table[i].name == name){
                    return i;
                }
                throw new Error("Table not found");
            }
        }

        var index = getIndex(tableName);
        axios.get(api).then(res =>{
        table[index].data = res.data;
        return table[index].data;
     }).catch(error => {
        throw error;
     });
     return null ;
}